import React from 'react';
import classes from './modal.css';

const modal=(props)=>(
    
    <div className={classes.ui} onClick={props.clicksub}>
       <div className={classes.modal}>
           <p className={classes.pra}>{props.children}</p>
           <button className={classes.yes} onClick={props.clickmdl}>بله</button>
           <button className={classes.cancel} onClick={props.clicksub}>خیر</button>
           </div> 
    </div>
);

export default modal;