import React from 'react';
import classes from './shopeicon.css'
import stt from '../../../assets/img/shopping-cart.png';
const Shopeicon= (props)=>{
   
    let store= null;
    if(props.bask !== 0){
        store= <div><span className={classes.sometotal}>{props.bask}</span><button onClick={props.orderhandler} className={classes.store}>
            <img className={classes.store_img} src={stt} placeholder='basket'/></button></div> 
    }
    return <div>{store}</div>;
}
export default Shopeicon;