import React from 'react';
import classes from './clickcard.css';

import {connect} from 'react-redux';
import {useParams,Link} from 'react-router-dom';
const Clickcard = (props)=>{
    const userId= useParams().userId;
    let load;
    
    if(props.flowers){
        load = props.flowers.filter(ie=> ie.id === userId);
    }
    
    return(
        <div className={classes.mahsol} style={{minHeight:"100vh",
            background: 'rgb(24, 190, 199)', width: '97%',
            border: '1px solid #111',
            margin: '1rem auto'}} items={load}>
            {load?load.map(clk=>(
                <div key={clk.id} className={classes.load}>
                    <img src={`data:image/jpeg;base64,${clk.photo}`} className={classes.wth}/>
                    <div className={classes.dive}>
                    <h4 className={classes.h4}>{clk.name}</h4>
                    <p className={classes.txt}>{clk.imageCover}</p></div>
                </div>
            )): <Link path='/' />}
        </div>
    )
}

const mapstatetoprops=state=>{
    return{
        person:state.card.person,
        flowers: state.get.flowers
    }
}
export default connect(mapstatetoprops,null)(Clickcard)