const Flowers= (props)=>{

  const [currentPage, setpage]= useState(1);
 

 const handleClick=(e)=>{
    setpage(JSON.parse(e.target.innerHTML))
    props.getdata('',e.target.innerHTML)
    
 }
 let renderPageNumbers=null
  let renderTodos= <Spinner/>
  if(props.flowers){
        const todos= props.flowers;
      
      renderTodos = props.flowers.map((mp, index) => (
                
        <div key={mp.id} className={classes.card}>
        <Link className={classes.link} to={`/${mp.id}/places`}>
        <div className={classes.divimg}><img src={`data:image/jpg;base64,${mp.photo}`}  className={classes.card_img}/></div>
        <h2 className={classes.card_name}>{mp.name}</h2>
        <p className={classes.card_text}>{mp.imageCover.slice(0,30)}<span className={classes.lnk}>...</span></p>
        </Link>
        <Basket click={index} ctd={mp.some}></Basket>
    </div>)
        
      );
  
      // Logic for displaying page numbers
      const pageNumbers = [];
      for (let i = 1; i <= Math.ceil(props.length / 8); i++) {
        pageNumbers.push(i);
      }
  
       renderPageNumbers = pageNumbers.map(number => {
        console.log(number , currentPage)
        return (
          <li className={ number === currentPage ? classes.active : classes.number}
            key={number}
            id={number}
            onClick={handleClick}
          >
            {number}
          </li>
        );
      });
      }

      const [mdl,setmdl]=useState(false)
    const orderhandler=()=>{
     props.history.push('/orders')
       // console.log(props)
    }
      
    const onmodalhandller=()=>{
      setmdl(true)
  }
  const offmodalhandller=()=>{
      setmdl(false);
  }

  let modalorder=null;
    if(mdl){
        modalorder=(
            <Modal onc={offmodalhandller} mdl={mdl} ></Modal>
        )
    }
  
      return (
        <div className={classes.flowers}>
          <div className={classes.section}>
            {modalorder}
          <div className={classes.Button}>
            <Button click={onmodalhandller} dis={props.total !==0 ? false:true}> ثبت</Button></div>
            {renderTodos}
            <div className={classes.paginate}>{renderPageNumbers}</div>
          </div>
            
            
         
          
        </div>
      );

}
const mapstatetoprops=state=>{
    return{
        card:state.card.person,
        filt:state.card.filter,
        flowers: state.get.flowers,
        length:state.get.length,
        total:state.get.someOrders
    }
}

const mapdispatchtoprops=dispatch=>{
  return{
    getdata: (va,num)=>dispatch(action.getdatainit(va,num ))
  }
}

export default connect(mapstatetoprops,mapdispatchtoprops)(Flowers);