import React,{useState,useEffect} from 'react';

import {Link} from 'react-router-dom';
import classes from './card.css';
import Basket from '../basket/basket';
import {connect} from 'react-redux';
import * as  action from '../../../store/action/index';
import Spinner from '../../UI/spinner/Spinner';


const Card =(props)=>{
    


const [filters, setfilter]=useState(props.flowers);

useEffect(()=>{
    
    if(props.fill !== ''){
        const fresh= props.flowers.filter(ie=>ie.name.includes(props.fill))
        setfilter(fresh);
    }else{
        setfilter(props.flowers);
    }
 
},[props.fill,props.flowers])
    let flowerUser=false;
    

    if(props.user){
        flowerUser = props.user.role === 'admin'? true: false
        
    }
    
   
    let cardinate= <Spinner/>;
    if(filters){
        const len= filters.slice(0,4)
        cardinate= len.map((mp,index)=>(
                   
            <div key={mp.id} className={classes.card}>
                
                
                {flowerUser?<span className={classes.delete} onClick={()=>props.clicksub(mp.id)}>X</span>:null}
                <Link className={classes.link} to={`/${mp.id}/places`}>
                <div className={classes.divimg}><img src={`data:image/jpg;base64,${mp.photo}`}  className={classes.card_img}/></div>
                <h2 className={classes.card_name}>{mp.name}</h2>
                <p className={classes.card_text}>{mp.imageCover.slice(0,20)}<span className={classes.lnk}>...</span></p>
                </Link>
                <Basket click={index} ctd={mp.some}></Basket>
            </div>
            
        ))
    }
    
    return(
        <div className={classes.section} >
            
           {cardinate}
        </div>
    );
};
const mapstatetoprops=state=>{
    return{
    card: state.card.person,
    bask: state.card.totalsome,
    fill: state.card.filter,
    flowers: state.get.flowers,
    user:state.get.user
}}
const dispatchtoprops=dispatch=>{
    return{
        onadd: (id)=>dispatch(action.setorder(id)),
        ondelete: (id)=> dispatch(action.deletepostinit(id)),
        getdata: ()=> dispatch(action.getdatainit())
    }
}
export default connect(mapstatetoprops,dispatchtoprops)(Card);