import React,{useEffect, useState} from "react";
import classes from './ListPosts.css';
import * as action from '../../../store/action/index';
import { connect } from "react-redux";
import fls from '../../../assets/img/arrows_circle_up.png';
import Spinner from "../../UI/spinner/Spinner";
import { NavLink } from "react-router-dom";

const ListPosts=(props)=>{
    const [List,onlist]=useState(false);
    const [id,setid]=useState();
    if(props.passage){
    if(!id){
        setid(props.passage[0]._id)
    }}
    useEffect(()=>{
        props.getname();
        
        if(props.passage){
           
            
            props.getinitpass(id)
        }
    },[props.getinitpass,id])
    
    let close= [classes.openList];
    if(List){
        close.push(classes.openListrotate)
    }
    const showList=()=>{
        onlist(!List)
    }
    let goo=null;

    
    let flowerUser=false;
    if(props.role){
        
        flowerUser = props.user.role === 'admin'? true: false
    }
    let spn= <Spinner></Spinner>;
    if(props.passage){
        spn= props.passage.map((mp)=><span className={classes.item}
         onClick={()=>setid(mp._id)}>{mp.name}    { flowerUser?<svg onClick={()=>props.passageDelete(mp._id)} version="1.1" xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20">
         <title>circle-with-cross</title>
         <path d="M10 1.6c-4.639 0-8.4 3.761-8.4 8.4s3.761 8.4 8.4 8.4 8.4-3.761 8.4-8.4c0-4.639-3.761-8.4-8.4-8.4zM14.789 13.061l-1.729 1.729-3.060-3.061-3.061 3.060-1.729-1.729 3.062-3.060-3.061-3.061 1.729-1.728 3.060 3.060 3.061-3.061 1.729 1.729-3.062 3.061 3.061 3.061z"></path>
         </svg>:null} </span>);
    }
    
    return(
      <React.Fragment>
            <img className={close.join(' ')} onClick={showList} src={fls} />
            {flowerUser?<NavLink to='/buildposts'><button className={classes.go} >ساخت پست</button></NavLink>:null}
            <NavLink to="/"><button className={classes.goback} >برگشت به صفحه اصلی</button></NavLink>
            <div onClick={()=>onlist(false)} className={List?classes.List:classes.ListClose}>
            
           {spn}
            </div>
            
            </React.Fragment>
    )
}
const mapstatetoprops=state=>{
    return{
        cookie:state.auth.cookie,
        passage:state.passage.passage,
        role:state.get.user
    }
}
const mapdispatchtoprops=dispatch=>{
    return{
        getname:()=>dispatch(action.psgpostinitget()),
        getinitpass:(id)=>dispatch(action.initpaas(id)),
        passageDelete:(id)=>dispatch(action.passageDelete(id)),
        
    }
}

export default connect(mapstatetoprops,mapdispatchtoprops)(ListPosts);