import React,{useState} from 'react';
import classes from './deawer.css';
import NavigationItem from '../UI/NavigationItem/NavigationItem';
import zab from '../../assets/img/circle-with-cross.svg';
import { connect } from 'react-redux';
import Navbar from '../UI/NavigationItems/navbar';

 const Drawer =(props)=>{
    const [navi, setnavi]=useState(false);
    const navihandller=()=>{
        setnavi(true)
    }
    const offnavihandller=()=>{
        setnavi(false)
    }
    
    let nav=null;
    if(navi){
        nav=  <div onClick={offnavihandller} className={classes.header}>
        <img src={zab} alt='zab' className={zab}/>
            <div className={classes.draw}>
                
           <Navbar/>
     </div>
 </div>
    }
    return(
        <div style={{position:'relative'}}>
            {nav}
           <button className={classes.option} onClick={navihandller}><span className={classes.span}>____ ____ ____</span></button>
          
        </div>
    );
 }
 const mapstatetoprops=state=>{
    return{
        person:state.card.basket,
        token:state.auth.token,
        flowers: state.get.flowers,
        user: state.get.user,
        order: state.order.order
    }
}
export default connect(mapstatetoprops,null)(Drawer);