import React from 'react';
import classes from './footer.css';
import insta from '../../assets/img/Instagram.Icons - LearnParsi (5).png';
import Telegram from '../../assets/img/Telegram.Icon. LearnParsi (1).png';




const Footer=(props)=>{
 //removeEventListener('#valih2')
 const remowe=()=>{
     
    delete('#valih2')
 }
    return(
        <div className={classes.footer}>
            <h2 id='valih2' className={classes.foot_text} onClick={()=>remowe()}>مارا در فضای مجازی دنبال کنید</h2>
            <div className={classes.foot_img_div}><a href='#' className={classes.link}><img src={insta} alt="instagram" className={classes.foot_img}/></a>
            <a href='#' className={classes.link}><img src={Telegram} alt="telegram" className={classes.foot_img}/></a>
            </div>
        </div>
    )
}
export default Footer;