import * as action from './actionTypes';

export const search=(val)=>{
    return{
        type: action.STARTORDER,
        val:val
    }
}
export const lessorder=(less)=>{
    return{
        type: action.LESSOREDER,
        less: less
    }
}
export const setorder =(add)=>{
    return{
        type: action.SETORDER,
        add:add
    }
}
export const initorder=(id)=>{
        return dispatch=>{
           /// dispatch(set(id))
        }
}
export const cardshow=()=>{
    return{
        type:action.CARDSHOW
    }
}
export const page=()=>{
    return{
        type:action.PAGESHOW
    }
}
