import axios from 'axios';
import * as action from './actionTypes';
import { showAlert } from './Alert';

export const psgstart= ()=>{
    return{
        type: action.PASSAGESTARTGET
    }
}
export const psgset=()=>{
    return{
        type: action.PASSAGESETGET
    }
}
export const psgfail=()=>{
    return{
        type:action.PASSAGEFAILGET
    }
}
export const psgpostinit=(data)=>{
    return dispatch=>{
        dispatch(psgstart());
        axios.post('https://valiflowers.herokuapp.com/api/v1/passage',data).then(res=>{
            if(res.data){
                dispatch(psgset())
            
                window.location.reload();
                window.location.replace('/');
                showAlert('success','پست با موفقیت ثبت شد')
            }

        }).catch(er=>{
            dispatch(psgstart())
            console.log('er passage')
        })
    }
}

export const psgstartget= ()=>{
    return{
        type: action.PASSAGESTART
    }
}
export const psgsetget=(dt,filt)=>{
    return{
        type: action.PASSAGESET,
        dt:dt,
        filt:filt
    }
}
export const psgfailget=()=>{
    return{
        type:action.PASSAGEFAIL
    }
}
export const psgpostinitget=()=>{
    return dispatch=>{
        dispatch(psgstartget());
        fetch('https://valiflowers.herokuapp.com/api/v1/passage',{
            method: 'GET',
        }).then(respo=> {return respo.json()}).then(res=>{
            const filt=res.data[0]._id;
            
            dispatch(psgsetget(res.data,filt));
            
        }).catch(er=>{
            dispatch(psgstartget());
            console.log('er passage grt');
        })
    }
}

export const passageDeleteset=()=>{
    return{
        type:action.PASSAGEDELETE
    }
}
export const passageDelete= (id)=>{
        return dispatch=>{
            fetch(`https://valiflowers.herokuapp.com/api/v1/passage/${id}`,{
                method: 'DELETE',
            }).then(res=>{
                dispatch(passageDeleteset())
                window.location.reload();
                window.location.replace('/');
            }).catch(er=>{
                console.log('er passage delete')
            })
        }
}

export const filterpassage=(filt)=>{
    return{
        type: action.FILTERPSG,
        filt:filt
    }
}


export const startpass=()=>{
    return{
        type:action.STARTPASSAGEONE,
    }
}
export const setpass=(data)=>{
    return{
        type:action.GETONEPASSAGE,
        data:data
    }
}

export const initpaas=(id)=>{
    return dispatch=>{
        dispatch(startpass());

        axios.get(`https://valiflowers.herokuapp.com/api/v1/passage/${id}`).then(res=>{
            dispatch(setpass(res.data.data))
       
        }).catch(er=>console.log(er))
    }
}
