import React from 'react';
import classes from './cardbest.css';
import Basket from '../cards/basket/basket';
import {connect} from 'react-redux';
import * as  action from '../../store/action/index';
import Spinner from '../UI/spinner/Spinner';
const Cardbest =(props)=>{
 


let best= <Spinner/>;
if(props.flowers){
    const slc=props.flowers.slice(0,2)
    best= slc.map((mp,index)=>(
        <div key={mp.id} className={classes.card}>

            <div className={classes.divimg}><img src={`data:image/jpeg;base64,${mp.photo}`}  className={classes.card_img}/></div>
            <h2 className={classes.card_name}>{mp.name}</h2>
            
            <div className={classes.baske}><Basket click={index}  ctd={mp.some}></Basket></div>
        </div>
    ))
}

 
   
    
    return(
        <div className={classes.scr}>
        <div className={classes.section}>
            {best}
         
        </div></div>
    );
};
const mapstatetoprops=state=>{
    return{
    card: state.card.person,
    bask: state.card.totalsome,
    fill: state.card.filter,
    flowers:state.get.flowers
}}
const dispatchtoprops=state=>{
    return{
        onadd: (id)=>state(action.setorder(id))
    }
}
export default connect(mapstatetoprops,dispatchtoprops)(Cardbest);