import React,{useState, useEffect} from 'react';
import classes from './name.css';
import ph6 from '../../assets/img/5.jpg';
import ph5 from '../../assets/img/4.jpg';
import ph4 from '../../assets/img/3.jpg';
import ph3 from '../../assets/img/2.jpg';
import { connect } from 'react-redux';




const Name=props=>{
    const {user}= props;
    const [img, setimg]=useState([ph6,ph5,ph4,ph3])
    const [num, setnum]= useState(0)
    useEffect(()=>{
        setTimeout(()=>{
            
            if(num >= (img.length - 1)){
                setnum(0)
            } else if(num < img.length){
                setnum( e=>{
                    return e + 1
                })
            }
        },[4000])
        
    },[num])
    
   
   let us= null;
   if(user){
       us= user
       console.log(user)
   }
    return(
        <div className={classes.name}>
            <div className={classes.divim} ><img className={classes.name_img} src={img[num]} /></div>
            <h1 className={classes.h1}>flowers</h1>
        </div>
    )
}
const mapstatetoprops=state=>{
    return{
        user: state.get.photo
    }
}
export default connect(mapstatetoprops,null)(Name);


