import React from 'react';
import classes from './basket.css';
import bask from '../../../assets/img/shopping-cart.png';
import {connect} from 'react-redux';
import * as action from '../../../store/action/index';
const Basket =(props)=>{
    
    const clickhandller=()=>{
        props.onadd(props.click)
        
        
    }
    const clickhandllerlees=()=>{
        props.onless(props.click)
    }
    

    let bool=null;
    if(props.ctd >= 1){
        bool= <button className={classes.less} onClick={clickhandllerlees}>حذف</button>
    }
    let some=null;
     if(props.ctd > 1){
        some=<span  className={classes.some}>{props.ctd}</span> 
    }

    return(
        <div className={classes.basket}>
            {bool}
            <div className={classes.some_box}>{some}<button  className={classes.svg} onClick={clickhandller}> افزودن به سبد <img src={bask}/></button></div>
            
        </div>
    );
};
const mapstatetoprops=state=>{
    return{
        card: state.card.person,
        id: state.card.basket
    }
}
const dispatchtoprops= dispatch=>{
    return{
        onadd: (id)=>dispatch(action.addfl(id)),
        onless:(id)=>dispatch(action.lessfl(id))
    }
}
export default connect(mapstatetoprops,dispatchtoprops)(Basket);