import axios from 'axios';
import * as action from './actionTypes';
import { showAlert } from './Alert';
const cookiejwt= document.cookie.split(' ')[0].replace('jwt=','')

export const startor=()=>{
    return{
        type: action.STARTSUB
    }
}
export const failor=()=>{
    return{
        type: action.FAILSUB
    }
}
export const setorder=()=>{
    return{
        type: action.SETSUB
    }
}

export const postinitorder= (data)=>{
    return dispatch=>{
        dispatch(startor());
            
        axios.post('https://valiflowers.herokuapp.com/api/v1/orders',data,{headers:{"Authorization" : `Bearer ${cookiejwt}`}}).then(res=>{
            if(res.data){
                window.location.reload();
                window.location.replace('/');
            showAlert("success","سفارش با موفقیت ثبت شد");
            dispatch(setorder());

 }       }).catch(err=>{
            showAlert("fail","اطلاعات وارد شده صحیح نمی باشد")
            dispatch(failor())
        })
    }
}

export const startgetor=()=>{
    return{
        type: action.STARTDATAOR
    }
}
export const failgetor=()=>{
    return{
        type: action.FAILDATAOR
    }
}
export const getorder=(data)=>{
    return{
        type: action.SETDATAOR,
        data:data
    }
}
export const getinitorder=()=>{
    return  dispatch=>{
        dispatch(startgetor);

         axios.get('https://valiflowers.herokuapp.com/api/v1/orders',{headers:{"Authorization" : `Bearer ${cookiejwt}`}}).then(res=>{

            dispatch(getorder(res.data.data))
            
        }).catch(err=>{
            dispatch(failgetor())
            console.log('err orders get')
        })
    }
}