import * as actionTypes from '../action/actionTypes';
import {UpdateObject} from './utility'
export const initialState={
        loading:false,
        error:false,
        newPosts:[],
        Posts:[]

}

const Reducer= (state=initialState, action)=>{

    switch(action.type){
        case actionTypes.BUILDPOSTSTART:
            return{
                ...state,
                loading:true,
                error:false
            }
        case actionTypes.BUILDPOSTSET: 
    
        return {
            ...state,
            newPosts: state.newPosts.push(action.data)
        }
        case actionTypes.BUILDPOSTFAIL:
            return{
                ...state,
                error:true,
                loading:false
            }
        case actionTypes.BUILDPOSTS:
            const val = [...state.Posts]
           
            return {
                ...state,
                Posts:val.concat({type:action.data, value:'',id:new Date()})
            }
        case actionTypes.CHANGINGPOSTS:
            const full=[...state.Posts];
            full[action.id]={...state.Posts[action.id], value: action.val}
        return{
            ...state,
            Posts:full
        }
        case actionTypes.DELETEPOSTS:
            const vl= [...state.Posts]
            const filter= state.Posts.filter(e=> action.id!==e.id)
            return{
                ...state,
                Posts: filter
            }     
        default : return state;
    }
}

export default Reducer;