import React from 'react';

import NavigationItem from '../NavigationItem/NavigationItem';
import { showAlert } from '../../../store/action/Alert';
import {connect} from 'react-redux';


const Navbar=(props)=>{
    let flowerfilter=[];
    if(props.flowers){
         flowerfilter= props.flowers.filter(e=>e.some > 0);
    }
    let flowerUser=false;
    if(props.user){
        flowerUser = props.user.role === 'admin'? true: false
    }
    

    const logoutHandller=async()=>{
        
        let date = new Date();
        date.setTime(date.getTime()+(100 * 3));

        document.cookie = 'jwt'  + " = "+ 'kdsjj' + " expires =" + 0
                showAlert('success' ,'logout success')
                
                window.location.reload();
        
    }
    let ord= null;
    if(props.order){
        ord= props.order.length > 0
        
    }
    console.log(flowerUser)
  
    return(
        <React.Fragment>
        <NavigationItem  link="/" exact>Home</NavigationItem>
            {!props.user?<NavigationItem link="/auth">auth</NavigationItem>:<p onClick={logoutHandller}><NavigationItem link="/logout">logout</NavigationItem></p>}
            {!flowerUser?null:<p><NavigationItem link="/buildpost">buildPost</NavigationItem></p>}
           { flowerfilter.length > 0? <p><NavigationItem link="/orders">orders</NavigationItem></p>: null}
           <p > <NavigationItem link="/passage">passage</NavigationItem></p>
           <p > <NavigationItem link="/flowers">flowers</NavigationItem></p>
            {ord?<p><NavigationItem link="/lastOrders">lastOrders</NavigationItem></p>:null}
            </React.Fragment>
        
    );
}
const mapstatetoprops=state=>{
    return{
        person:state.card.basket,
        token:state.auth.token,
        flowers: state.get.flowers,
        user:state.get.user,
        order: state.order.order
    }
}
export default connect(mapstatetoprops,null)(Navbar);