import * as actiontype from '../action/actionTypes';

export const initialstate={
    loading:false,
    error:false,
    passage:null,
    load:false,
    err:false,
    filter: null,
    passageone:null
    
}

const Reducer=(state=initialstate,action)=>{
        switch(action.type){
            case actiontype.PASSAGESTART:
                return{
                    ...state,
                    loading:true
                }
             case actiontype.PASSAGEFAIL:
                 return{
                     ...state,
                     loading:false,
                     error:true
                 }
             case actiontype.PASSAGESET:
                
                    return{
                        ...state,
                        loading:false
                        ,
                        passage:action.dt,
                        filter:action.filt
                        
                    }
           case actiontype.PASSAGESTARTGET:
                     return{
                         ...state,
                         load:true
                     }
            case actiontype.PASSAGEFAILGET:
                        return{
                            ...state,
                            err:false,
                            load:false
                        }
        //    case actiontype.PASSAGESET:
        //        console.log(action.dt)
        //              return{
        //          ...state,
        //          load:true,
        //          passage:action.dt
        //     } 
            case actiontype.PASSAGEDELETE:
                return{
                    ...state,
                    
                }
            case actiontype.FILTERPSG:
                return{
                    ...state,
                    filter: action.filt
                }
            case actiontype.STARTPASSAGEONE:
                return{
                    ...state,
                    load:true
                }
                case actiontype.GETONEPASSAGE:
                    return{
                        ...state,
                        load:false,
                        passageone:action.data
                    }
            default: return state
        }
}
export default Reducer;