import React, {useState} from 'react';
import srh from '../../assets/img/search.svg'
import classes from './search.css';
import {connect} from 'react-redux';
import * as action from '../../store/action/index';
const Search =(props)=>{
    
    const [input, setinput]=useState('');
  
     
    

    const srchhandller=()=>{
        props.onsearch(input)
        props.getflowers(input);
    }
    
    return(
        <div className={classes.search}>
            <input className={classes.input_search} type='text' placeholder="search"
            value={input}
            onChange={e=>setinput(e.target.value)}
            
           /// onFocus={pagehandller}
              />
            <img className={classes.img_search} src={srh} onClick={srchhandller} />
            
        </div>
    );
}
const mapstatetoprops=state=>{
    return{
        person:state.card.person,
        fill: state.card.filter,
        flowers:state.get.flowers
    }
}
const mapdispatchtoprops=dispatch=>{
    return{
        onsearch:(il)=>dispatch(action.search(il)),
        onpage: ()=>dispatch(action.page()),
        getflowers:(srch)=>dispatch(action.getdatainit(srch))
    }
}
export default connect(mapstatetoprops,mapdispatchtoprops)(Search);