import * as actiontype from '../action/actionTypes';
import {updateObject} from '../action/spread';


const initialstate={
    token:null,
    userid:null,
    loading:false,
    error:false,
    logout: null,
    auth:null,
    loadcookie:false,
    errorcookie:false,
    cookie: false
}

const starta= (state, action)=>{
    
   return updateObject(state,{loading:true})
}
const seta= (state, action)=>{
    
    return updateObject(state,{token:action.tok , loading:false})
}
const faila= (state, action)=>{
    return updateObject(state, {error:true})
}
const logout= (state, action)=>{
    return updateObject(state, {path:'/'})
}
const startco= (state, action)=>{
    return updateObject(state, {loadcookie:true})
}
const setco= (state, action)=>{
    return updateObject(state, {loadcookie:false, cookie:action.cookie})
}
const failco= (state, action)=>{
    return updateObject(state, {loadcookie:false, errorcookie:true})
}
const Reducer = (state=initialstate, action)=>{

    switch(action.type){
        case actiontype.STARTAUTH: return starta(state,action);
        case actiontype.SETAUTH: return seta(state, action);
        case actiontype.FAILAUTH: return faila(state, action);
        case actiontype.LOGOUTAUTH: return logout(state, action);
        case actiontype.STARTCOOKIE: return startco(state, action);
        case actiontype.SETCOOKIE: return setco(state, action);
        case actiontype.FAILCOOKIE: return failco(state, action);
        default: return state;
    }
}
export default Reducer;