import * as actionType from '../action/actionTypes';

export const initialState={
    flowers:null,
    totalPrice: 0,
    loading: false,
    length:null,
    error: false,
    load:false,
    er: false,
    user:null,
    someOrders:0
}

const Reducer=(state= initialState, action)=>{

    switch(action.type){
        case actionType.ADDFL:
            const obj= [...state.flowers];
            obj[action.id]={
                ...state.flowers[action.id],
                some:  state.flowers[action.id].some + 1
            }
            const price= obj[action.id].some * obj[action.id].price
            return{
                ...state,
                flowers: obj,
                totalPrice: price,
                someOrders: state.someOrders + 1
            }
        case actionType.LESSFL:
            
            const obg= [...state.flowers];
            obg[action.id]={
                ...state.flowers[action.id],
                some: state.flowers[action.id].some > 0? state.flowers[action.id].some - 1: state.flowers[action.id].some === 0,
            }
            return{
                ...state,
                flowers:obg,
                someOrders: state.someOrders - 1
            }
        case actionType.GETDATASTART:
            return{
                ...state,
                loading:true
            }
        case actionType.GETDATAFAIL:
            return{
                ...state,
                error:false
            }
        case actionType.GETDATASET:
            return{
                ...state,
                flowers: action.posts,
                length: action.length
            }
            
            case actionType.DELETEPOST:
         return{
           ...state,
           
         }
         case actionType.STARTUSER:
            return{
                ...state,
                load:true
            }
        case actionType.FAILUSER:
            return{
                ...state,
                er:false,
                load:false
            }
        case actionType.GETUSER:
            return{
                ...state,
                user:action.user,
                load:false   
            }
        default: return state;
    }
}
export default Reducer;