import React,{Suspense, useEffect} from 'react';

import ShopBuilder from './continar/ShopBuilder/ShopBuilder';
import {Route, Switch,withRouter,Link} from 'react-router-dom';
import {connect} from 'react-redux';
import Clickcard from './components/clickcard/clickcard';
import * as action from './store/action/index';
import Flowers from './continar/FlowersBuilder/FlowersBuilder';
import Spinner from './components/UI/spinner/Spinner';
import Posts from './components/Posts/Posts';
import BuildPost from './components/Posts/BuildPost/BuildPost';

const dotenv=require('dotenv')
dotenv.config({path:'../.env'})

const buildPost=React.lazy(()=>{
    return import('./components/buildPost/buildPost');
}); 
const lastOrders= React.lazy(()=>{
  return import('./components/Orders/Orders')
})
const logout= React.lazy(()=>{
  return import('./components/logout/logout')
})
const form= React.lazy(()=>{
    return import('./continar/form/form')
})
const auth= React.lazy(()=>{
  return import('./continar/auth/auth');
})
const cardOrder=React.lazy(()=>{
  return import('./components/cards/cardorder/cardorder')
})
function App(props){
  

  useEffect(()=>{
    props.getusersdata()
  },[props.getusersdata])


  useEffect(()=>{
    props.getcookie()
  },[ props.getcookie])

  useEffect(()=>{
    if(props.user){
      props.getinitorder();
  }
  },[props.getinitorder,props.user])
  useEffect(()=>{
    
    
  
    if(!props.getdata){
      props.getdatainit();
      if(props.cookie){
        props.ongetusersdata();
      }
      
    }
    
     
  },[props.getdatainit, props.getdata, props.ongetusersdata])
      

    let buildP=null;
    if(props.user){
     // buildP=props.user.role !== "admin"?null:<React.Fragment><Route path="/buildpost" component={buildPost}/><Route path="/buildposts"  component={BuildPost}/></React.Fragment>
     buildP=props.user.role
    }

    
    
   let apptoken= (
    <Switch><Route path="/"  exact  component={ShopBuilder}/>
    <Route path="/logout" exact component={logout}/>
    <Route path="/auth" exact component={!props.cookie?auth:null} />
    <Route path="/buildposts" exact component={buildP?BuildPost:ShopBuilder}/>
  <Route path="/orders" exact component={cardOrder}/>
  <Route path="/form" exact component={props.user?form: auth}/>
  <Route path='/lastOrders' exact component={props.order?lastOrders:ShopBuilder}/>
  <Route path="/passage" exact component={Posts} />
  <Route path="/buildpost" exact component={buildP?buildPost:ShopBuilder}/>
  <Route path="/flowers" exact component={Flowers} />
  <Route path="/:userId/places" exact component={Clickcard}/>
  <Route path="/post" exact component={Posts}/>
  <Route path='/*' render={()=><ShopBuilder/>}/>
        <Link from="*" to="/"/>
  </Switch>
  )
    
 
    return (<div >
      <Suspense fallback={<Spinner/>}> {apptoken}</Suspense>
      </div>)
}
const mapstatetoprops=state=>{
  return{
      search:state.card.search,
      order: state.order.order ,
      getdata: state.get.flowers,
      user:state.get.user,
      auth: state.auth.auth
  }
}
const mapdispatchtoprops=dispatch=>{
  return{
    
    getdatainit:()=> dispatch(action.getdatainit()),
    ongetusersdata: ()=> dispatch(action.getusersdata()),
    getcookie:()=>dispatch(action.getcookie()),
    getusersdata:()=>dispatch(action.getusersdata()),
    getinitorder:()=>dispatch(action.getinitorder())
  }
}
export default withRouter(connect(mapstatetoprops,mapdispatchtoprops)(App));