import React,{useState,useEffect} from "react";
import { connect } from "react-redux";
import classes from './builder.css';

import * as action from '../../../store/action/index';
import compress from 'react-image-file-resizer';

const BuildPost=(props)=>{


    const [choicetag,setchicetag]=useState();
    
    const [num,addnum]=useState(0);
    const [lng,setlng]=useState(false);
    
    
    
    const addarr=(type)=>{
       
    
        props.building(type);
       
    }

    const changehandller=(e,id)=>{

        props.changing(e,id)
        
    }
    const changefilehandller=(e)=>{
        
        //e.preventDefault()
       compress.imageFileResizer(
            e.target.files[0], // the file from input
     530, // width
     450, // height
     "JPEG", // compress format WEBP, JPEG, PNG
     70, // quality
     0, // rotation
     (uri) => {
       // You upload logic goes here
      props.changing(uri,lng)
     },
     "base64" // blob o
        )
      e.preventDefault()
       
      
    }

    let filt;
    useEffect(()=>{
       filt= props.posts.filter(mi=>mi.type === 'img')
        addnum(filt?filt.length:0)
    },[props.posts])
    const changename=(e)=>{
        setchicetag(e.target.value);
       
    }

    const posthandller=()=>{
        const data={
            data:props.posts,
            name:choicetag
        }
        props.psgpostinit(data)
    }
    return(
        <div className={classes.buildpost}>
            <div className={classes.box_choice}>
            <button className={classes.choice} disabled={props.length <= 20?true:false} onClick={()=>addarr('text')}>نوشتن متن</button>
            <button className={classes.choice} disabled={props.length <= 20?true:false} onClick={()=>addarr('h1')}>سر تیتر</button>
             <button className={classes.choice} disabled={num >= 4?true:false} onClick={()=>addarr('img')}>عکس</button>
             <input type="text" className={classes.name} 
              placeholder="نام پست خود را وارد کنید" onChange={(e)=>changename(e)}/>
            </div>
            <div className={classes.builder}>
                {props.posts.map((mp,index)=>(<div>
                  { mp.type === 'text'? <div className={classes.text}><textarea placeholder="متن خود را وارد کنید" 
                  className={classes.text_input} type='text'
              onChange={(e)=>changehandller(e.target.value,index)}/>
             <span className={classes.remowe} onClick={()=>props.remoweing(mp.id)}>X</span></div>:null}
             {mp.type=== 'h1'?<div className={classes.text}><textarea className={classes.h1_input} placeholder="سر تیتر خود را وارد کنید"
              type='text' 
                 onChange={(e)=>changehandller(e.target.value,index)}/>
                 <span className={classes.remowe} onClick={()=>props.remoweing(mp.id)}>X</span></div>:null}
             {mp.type==='img'?<div className={classes.text}>< label onClick={()=>setlng(index)}  className={classes.img_input} title='فقط یک عکس انتخاب کنید' for="name">
                 {"عکس انتخاب شدعکس مورد نظر راانتخاب کنید"}</label>
             <input
               type='file' id='name' name='name' style={{display:'none'}}
               onChange={(e)=>changefilehandller(e,index)}/>
             <span className={classes.remowe} onClick={()=>{props.remoweing(mp.id)}}>X</span></div>:null}
                </div>))}
                <div className={classes.newitem}>
                {props.posts.length !==0 && choicetag?<button className={classes.choice} onClick={()=>posthandller()}>ذخیره</button>:null}
                </div>
               
            </div>
        </div>
    )
}
const mapstatetoprops=state=>{
    return{
        posts:state.newPost.Posts
    }
}
const mapdispatchtoprops=dispatch=>{
    return{
        building:(data)=>dispatch(action.builderinit(data)),
        changing:(e,id)=>dispatch(action.changing(e,id)),
        remoweing:(id)=>dispatch(action.remowinit(id)),
        psgpostinit:(dt)=>dispatch(action.psgpostinit(dt))
    }
}
export default connect(mapstatetoprops,mapdispatchtoprops)(BuildPost)