import React from 'react';
import classes from './modal.css';
import {connect} from 'react-redux';
import {NavLink} from 'react-router-dom';
import {Transition} from "react-transition-group";
import Spinner from '../UI/spinner/Spinner';
const Modal=(props)=>{
   
   let mdl= <Spinner/>

   if(props.flowers){
       mdl=<React.Fragment> <div className={classes.Modal}>
       {props.flowers.map(we=>(
           <p key={we.id} className={classes.someorder}>{we.some !==0? [we.name +' '+':'+' '+we.some]: null}</p>
       ))}</div>
       <div><NavLink to="/form"><button className={classes.ok}  >ادامه</button></NavLink>
       <button className={classes.cancel} onClick={props.onc} >لغو</button></div></React.Fragment>
   }
    //const order= Object.keys(mod).map((er)=>{
        //return <p key={er.id}>{er.id}</p>  })
    return(
    <div className={classes.vali}  onClick={props.onc}>
        <div className={classes.mode} >
            {mdl}
            
        </div>
        
    </div>
    );
}
const mapstatetoprops=state=>{
    return{
        pers:state.card.person,
        flowers:state.get.flowers
    }
}
export default connect(mapstatetoprops,null)(Modal);