const Reducer=(state= initialstate, action)=>{
   switch(action.type){
       case actiontype.SETORDER:
            const add= state.person[action.add].some ;  
              
       const updatedProducts = [...state.person];
       updatedProducts[action.add] = {
         ...state.person[action.add],
         some: add + 1,
         condition: true
       };
       return {
         ...state,
         person: updatedProducts,
         totalsome: state.totalsome + 1,
         basket:true
       };
       case actiontype.LESSOREDER: 
       const less= state.person[action.less].some ;    
       const updatedless = [...state.person];
       let bool= false;
       if(updatedless[action.less].some !== 1){
        bool= true
       }
       updatedless[action.less] = {
         ...state.person[action.less],
         some: less - 1,
         condition: bool
       };
       console.log(less)
       return{
        ...state,
        person: updatedless,
        totalsome: state.totalsome - 1,
        basket:bool
       }
       case actiontype.STARTORDER: 
       return{
         ...state,
         filter:action.val,
         
       }
       case actiontype.PAGESHOW: return{
         ...state,
         search:true
        }
       case actiontype.CARDSHOW: 
       return{
         ...state,
         search: false
       }
       
       default: return state;
   }
}

export default Reducer;