import axios from 'axios';
import * as action from './actionTypes';
import { showAlert } from './Alert';


const cookiejwt= document.cookie.split(' ')[0].replace('jwt=','');

export const getstart=()=>{

    return{
        type: action.GETDATASTART
    }
}

export const getrecive=(posts,img)=>{
    return{
        type: action.GETDATASET,
        posts:posts,
        length:img
    }
}
export const getfail=()=>{
    return{
        type: action.GETDATAFAIL
    }
}
export const getdatainit=(search,page)=>{
    return dispatch=>{
        dispatch(getstart());
        let src="";
        axios.get(`https://valiflowers.herokuapp.com/api/v1/flowers?page=${page?page:1}&limit=8&fields=${search?search:''}`).then(response=> {
          
            dispatch(getrecive(response.data.data,response.data.length));
         
        }).catch(err=>{
            dispatch(getfail())
            console.log('err flower')
        })
        
    }
}
export const addfl=(id)=>{
    return{
        type:action.ADDFL,
        id:id
    }
}
export const lessfl= (id)=>{
    return{
        type:action.LESSFL,
        id:id
    }
}

export const deletepost=()=>{
    return{
        type:action.DELETEPOST,
        
    }
}
export const deletepostinit =(id)=>{

    return async dispatch=>{
       await axios(`https://valiflowers.herokuapp.com/api/v1/flowers/${id}`,{
            method: 'DELETE',
        }).then(res=>{
            dispatch(deletepost())
            window.location.reload()
        }).catch(()=>console.log('delete error'));
    }
}





export const startauth =()=>{

    return{
        type: action.STARTAUTH
    }

}
export const setauth =(tok)=>{
    return{
        type: action.SETAUTH,
        tok:tok
    }
}
export const failauth =()=>{
    return{
        type: action.FAILAUTH
    }
}

export const logout =()=>{
    // localStorage.removeItem('userid')
    // localStorage.removeItem('token');
    // localStorage.removeItem('expirationDate')
    return{
        type: action.LOGOUTAUTH
    }
}

export const authinit=(email,password,name,passwordConfirm, isSineup)=>{
        return  dispatch=>{
            dispatch(startauth());
            const dt= {
                name,email,password,passwordConfirm
            }
            let auth= 'sineup';
            if(!isSineup){
                auth = 'login'
            }
            
         
            fetch(`https://valiflowers.herokuapp.com/api/v1/users/${auth}`, {
  body: JSON.stringify(dt),
  credentials: "same-origin",
  method: "post",
  url: "https://valiflowers.herokuapp.com",
  headers: new Headers({
    "content-type": "application/json",
    "access-control-allow-credentials": "true",
  }),
})
  .then((data) => data.json())
  .then((data) => {
   
    if(data.data){

        let date = new Date();
        date.setTime(date.getTime()+(1 * 1000 * 60 * 60 * 24 * 90));

    document.cookie= 'jwt'  + " = "+ data.token + " expires =" + date.toGMTString()
      showAlert('success', 'با موفقیت انجام شد')
        window.location.reload();
        window.location.replace('/')}
  }).catch(r=>{
      showAlert('fail',"اطلاعات وارد شده صحیح نمی باشد")
  })
                
            
            }
}

export const cookiestart= ()=>{
    return{
        type: action.STARTCOOKIE
    }
}
export const setcookie=(cok)=>{
    return{
        type: action.SETCOOKIE,
        cookie:cok
    }
}
export const cookiefail= ()=>{
    return{
        type: action.FAILCOOKIE
    }
}
export const getcookie= ()=>{
    return dispatch=>{
        dispatch(cookiestart());
        fetch(`https://valiflowers.herokuapp.com/api/v1/users/cookie`,{
            method:"GET",
            headers:{"Authorization" : `Bearer ${cookiejwt}`},
            
        })
        .then(res=>{
          
            dispatch(setcookie(res.data.cookie));
            
        }).catch(er=>{
            console.log(er)
        })
    }
}


export const startuser=()=>{
    return{
        type:action.STARTUSER
    }
}
export const setuser=(user)=>{
    return{
        type:action.GETUSER,
        user:user
    }
}
export const failuser=()=>{
    return{
        type:action.FAILUSER
    }
}
export const getusersdata=()=>{
    return  dispatch=>{
        dispatch(startuser());
        
    axios.get(`https://valiflowers.herokuapp.com/api/v1/users`,{headers:{"Authorization" : `Bearer ${cookiejwt}`}}).then(response=> {
  
    
    dispatch(setuser(response.data.data))
          
            
        }).catch(err=>{
            dispatch(failuser())
            console.log('user get false')
        })
        
    }
}
