import * as actiontype from '../action/actionTypes';
import {updateObject} from '../action/spread';

export const initialstate ={
    order:null,
    loading:false,
    error:false,
    path: null,
    err:false,
    load:false
}
const startor=(state,action)=>{
   return updateObject(state,{loading:true})
}
const setor=(state,action)=>{
    return updateObject( state, {path:'/',loading:false
    } );
}

const failor=(state,action)=>{
   return updateObject(state,{error:true,loading:false})
} 

const getor=(state,action)=>{
   return updateObject(state,{order:action.data})
} 
const startgror=(state,action)=>{
    return updateObject(state,{load:true})
 }
 
 const failgror=(state,action)=>{
    return updateObject(state,{err:true})
 }
const Reducer =(state=initialstate,action)=>{

    switch(action.type){
        case actiontype.STARTSUB :return startor(state,action) ;
        case actiontype.SETSUB :return setor(state,action);
        case actiontype.FAILSUB :return failor(state,action);
        case actiontype.SETDATAOR :return getor(state,action);
        case actiontype.FAILDATAOR :return failgror(state,action);
        case actiontype.STARTDATAOR :return startgror(state,action);
        default: return state;
    }
}
export default Reducer;