import React,{useEffect, useState} from 'react';
import Cards from '../../components/cards/cards';
import Passage from '../../components/passage/passage';
import Name from '../../components/name/name';
import classes from '../../index.css';
import {connect} from 'react-redux';
import Shopeicon from '../../components/UI/shopeicon/shopeicon';
import * as action from '../../store/action/index';
import Modal from '../../components/modal/modal';
import Cardbest from '../../components/cardbest/cardbest';
import Footer from '../../components/footer/footer';
import Layout from '../Layout/Layout';

    
const ShopBuilder =(props)=>{

    useEffect(()=>{
        console.log()
       props.oncookieexist()
       
       
    },[ props.flowers, props.oncookieexist])
    const [mdl,setmdl]=useState(false)
    const orderhandler=()=>{
     props.history.push('/orders')
       // console.log(props)
    }
   
    const onmodalhandller=()=>{
        setmdl(true)
    }
    const offmodalhandller=()=>{
        setmdl(false);
    }
    let srh=null;
    if(props.search === ''){
        srh=<div><div className={classes.text} onClick={offmodalhandller} >
        <Shopeicon bask={props.bask} orderhandler={orderhandler} card={props.card} ></Shopeicon> 
        <Name></Name>
        
 <Passage></Passage>
 
 </div><Cardbest></Cardbest> </div>
    }
    
    let modalorder=null;
    if(mdl){
        modalorder=(
            <Modal onc={offmodalhandller} mdl={mdl} ></Modal>
        )
    }
    return(
        <Layout>
        <div  style={{position:'relative'}}>
            {modalorder}
             {srh}
             
            <Cards   click={onmodalhandller}></Cards>
            <Footer/>
            
        </div></Layout>
    )
}
const mapstatetoprops=state=>{
    return{
    card: state.card.person,
    bask: state.card.totalsome,
    user: state.get.user,
    search:state.card.filter ,
    flowers: state.get.flowers
}}
const dispatchtoprops=dispatch=>{
    return{
        onsrh:()=>dispatch(action.cardshow()),
        ongetinitorder: ()=>dispatch(action.getinitorder()),
        oncookieexist: ()=>dispatch(action.getcookie())
    }
}
export default connect(mapstatetoprops,dispatchtoprops)(ShopBuilder);