import React,{useState} from 'react';
import classes from './cards.css';
import Card from './card/card';
import Button from '../UI/Button/Button';
import {connect} from 'react-redux';
import {CSSTransition} from 'react-transition-group';
import Modal from '../UI/modal/modal';
import * as action from '../../store/action/index';
const Cards=(props)=>{
    const [mdl, setmdl]= useState(false);
    const [iddl, setiddl]= useState();
    const clickhandller=(id)=>{
        setmdl(true);
        setiddl(id);
    }
    const clickhandller2=()=>{
        setmdl(false);
    }
    const deletehandller=()=>{
        props.ondelete(iddl);
        props.getdata();
    }
    let showmdl= null;
    if(mdl){
        showmdl=<Modal clickmdl={deletehandller} clicksub={clickhandller2}> از حذف این پست مطمئن هستید؟</Modal>
    }
 
    return(
        <div className={classes.cards}>
            {showmdl}
            <Card clicksub={clickhandller}></Card>
            <div className={classes.Button}><Button click={props.click} dis={props.total !==0 ? false:true}> ثبت</Button></div>
        </div>
    )
}
const mapstatetoprops=state=>{
    return{
        total:state.get.someOrders
    }
}
const dispatchtoprops=dispatch=>{
    return{
        onadd: (id)=>dispatch(action.setorder(id)),
        ondelete: (id)=> dispatch(action.deletepostinit(id)),
        getdata: ()=> dispatch(action.getdatainit())
    }
}
export default connect(mapstatetoprops,dispatchtoprops)(Cards);