import React from 'react';
import classes from './passage.css';
import flower from '../../assets/img/grafiki.jpg'

const Passage=props=>{

    return(
        <div className={classes.name}>
            <div className={classes.divimg}><img src={flower} className={classes.img_psg} /></div>
            <h1 className={classes.h1}>خرید آسان و سریع</h1>
        </div>
    )
}
export default Passage;