
import axios from 'axios';
import * as action from './actionTypes';
import { showAlert } from './Alert';

export const builsStart=()=>{
    return{
        type:action.BUILDPOSTSTART
    }
}
export const setBuild= (data)=>{
    return{
        type:action.BUILDPOSTSET,
        data:data
    }
}
export const buildfail=()=>{
    return{
        type: action.BUILDPOSTFAIL
    }
}

export const initbuldpost=(data)=>{
    return dispatch=>{
        dispatch(builsStart())
        
        axios({url:'https://valiflowers.herokuapp.com/api/v1/flowers',method:"POST",data:data})
        .then(res=>{
            if(res.data.data){
                showAlert('success','پست با موفقیت ساخته شد')
            dispatch(setBuild(res.data))
            window.location.reload();
            window.location.replace("/")
        }
           
        }).catch(re=>{
            dispatch(buildfail())
            showAlert('fail','اطلاعات وارد شده صحیح نمی باشد')
        })
    }
} 


export const buildsets=(data)=>{

    return{
        type:action.BUILDPOSTS,
        data:data
    }
}

export const builderinit=(type)=>{
    return dispatch=>{
       
        dispatch(buildsets(type))
    }
}

export const changebuild=(val,id)=>{
    return{
        type:action.CHANGINGPOSTS,
        id:id,
        val:val
    }
}
export const changing=(val,id)=>{
    return dispatch=>{
        
        dispatch(changebuild(val,id))
    }
}

export const setremow=(id)=>{
    return{
        type:action.DELETEPOSTS,
        id:id
    }
}
export const remowinit=(id)=>{
    return dispatch=>{
        dispatch(setremow(id));
    }
}