import React, { useState } from "react";
import ListPosts from "./ListPosts/ListPosts";
import classes from './Posts.css';
import { connect } from "react-redux";
import Spinner from "../UI/spinner/Spinner";

const Posts=(props)=>{

   

    let psg=<Spinner/>
    if(props.passageone){
        psg = props.passageone.data.map((mp)=>
                <React.Fragment>
        {mp.type === 'img' && mp.value !== ''?<div className={classes.post_img_box}>
                    <img className={classes.post_img} src={mp.value}/>
                </div>:null}
                {mp.type==='h1'?<div className={classes.post_h3_box}>
                    <h3 className={classes.post_h3}>{mp.value}</h3>
                </div>:null}
                {mp.type === 'text'?<div className={classes.post_text_box}>
                    <p className={classes.post_text}>{mp.value}</p>
                </div>:null}
                </React.Fragment> )
    }
    return(
        <React.Fragment>
<ListPosts user={props.user}/>

<section className={classes.post_section}>
<div className={classes.post_target}>
        {psg}
        </div>
</section>

        </React.Fragment>
        
    )
}
const mapstate=state=>{
    return{
        user:state.get.user,
        passageone:state.passage.passageone,
        load:state.passage.load
    }
}
export default connect(mapstate,null)(Posts);